<template>
	<b-navbar toggleable="sm" id="nav" class="border-bottom">
		<b-navbar-toggle target="nav-collapse"/>
		<b-collapse id="nav-collapse" is-nav>	
			<b-navbar-nav>		
				<b-nav-item-dropdown text="File">
					<b-dropdown-item-button  @click="clearAll">
						<b-icon-file-earmark-plus class="mr-2" />
						<span>New</span>						
					</b-dropdown-item-button>
					<b-dropdown-item-button  @click="openFileDialog">
						<b-icon-upload class="mr-2" />
						<span>Open...</span>						
						<b-form-file plain 
							id="fileOpen" 
							style="display: none" 							
							accept=".json"                
							@input="fileLoad">
						</b-form-file>					
					</b-dropdown-item-button>
					<b-dropdown-item-button @click="fileSave">
						<b-icon-download class="mr-2" />
						<span>Save...</span>						
					</b-dropdown-item-button>					
					<b-dropdown-divider/>
					<b-dropdown-item-button v-b-modal.modalFileImport>
						<b-icon-box-arrow-in-left class="mr-2" />
						<span>Import context stratigraphy (CSV)...</span>
						<ModalFileImport />						
					</b-dropdown-item-button>					
					<b-dropdown-item-button disabled>
						<b-icon-box-arrow-right class="mr-2" />
						<span>Export context stratigraphy (CSV)...</span>
					</b-dropdown-item-button>
					<b-dropdown-divider/>
					<b-dropdown-item-button disabled>
						<b-icon-box-arrow-right class="mr-2" />
						<span>Export diagram (PNG)...</span>
					</b-dropdown-item-button>					
				</b-nav-item-dropdown>
				<b-nav-item-dropdown text="View">
					<b-dropdown-item-button disabled @click="zoomIn">
						<b-icon-zoom-in class="mr-2" />
						<span>Zoom in</span>
					</b-dropdown-item-button>
					<b-dropdown-item-button disabled @click="zoomOut">
						<b-icon-zoom-out class="mr-2" />
						<span>Zoom out</span>
					</b-dropdown-item-button>
					<b-dropdown-item-button @click="zoomToFit">
						<b-icon-arrows-fullscreen class="mr-2" />
						<span>Zoom to fit</span>
					</b-dropdown-item-button>
					<b-dropdown-divider/>
					<b-dropdown-item-button @click="redoLayout">
						<b-icon-diagram-3 class="mr-2" />
						<span>Redo Layout</span>
					</b-dropdown-item-button>
				</b-nav-item-dropdown>
				<b-nav-item-dropdown text="Help">
					<b-dropdown-item-button v-b-modal.modalAbout>
						<b-icon-question-circle class="mr-2" />
						<span>About...</span>
						<ModalAbout />					
					</b-dropdown-item-button>
				</b-nav-item-dropdown>
			</b-navbar-nav>
		</b-collapse>
		<!--<b-navbar-brand href="#" id="brand" tag="h1" class="mb-0 primary">The Matrix</b-navbar-brand>-->
		<b-navbar-brand href="#">
			<img src="phaser-spacedout-logo.png" height="30" alt="PHASER"/>
		</b-navbar-brand>
	</b-navbar>	
</template>

<script>
//import ModalFileOpen from '@/components/ModalFileOpen'
import ModalFileImport from '@/components/ModalFileImport'
import ModalAbout from '@/components/ModalAbout'
import moment from 'moment'

export default {
	name: 'MenuBar',
	components: {
		//ModalFileOpen,
		ModalFileImport,
		ModalAbout
	},
	props: { },
	data: function() {
		return { }
	},
	computed: {	},
	methods: { 
		openFileDialog() {
			// display the dialog
			document.getElementById("fileOpen").click() 
		},		

		clearAll(){
			let self = this
			self.$bvModal.msgBoxConfirm('Clear all current data, are you sure?')
				.then(value => {
					if(value) self.$store.dispatch('clearAll') 
				})	
		},
		fileLoad(file){
			const self = this            
			if(!file) return
			
            const reader = new FileReader()
            reader.onload = function(e) { 
                const fileContents = JSON.parse(e.target.result)
                self.$store.dispatch('loadPhaserData', fileContents)				
            }
            reader.readAsText(file)
		},
		fileSave() {
			const data = {
				nodes: this.$store.getters.cyNodes.map(n => n.data),
				edges: this.$store.getters.cyEdges.map(e => e.data)
			}
			const fileName = `phaser-${ moment().format("YYYYMMDDHHmmss") }.json`
            this.saveToFile(JSON.stringify(data), fileName)
		},
		// this function from https://forum.vuejs.org/t/saving-form-data/38714
        saveToFile(jsonData, fileName) {
            let blob = new Blob([jsonData], { type: 'text/plain;charset=utf-8;' })
            if (navigator.msSaveBlob) { // IE 10+
                navigator.msSaveBlob(blob, fileName)
            } else {
                let link = document.createElement('a')
                if (link.download !== undefined) { // feature detection
                    // Browsers that support HTML5 download attribute
                    let url = URL.createObjectURL(blob)
                    link.setAttribute('href', url)
                    link.setAttribute('download', fileName)
                    link.style.visibility = 'hidden'
                    document.body.appendChild(link)
                    link.click()
                    document.body.removeChild(link)
                }
            }
        },
		zoomIn(){
			// fire event to event bus - matrix diagram should respond to this
			this.$root.$emit('zoomIn') 
		},
		zoomOut(){
			// fire event to event bus - matrix diagram should respond to this
			this.$root.$emit('zoomOut') 
		},
		zoomToFit(){
			// fire event to event bus - matrix diagram should respond to this
			this.$root.$emit('zoomToFit') 
		},
		redoLayout(){
			// fire event to event bus - matrix diagram should respond to this
			this.$root.$emit('redoLayout') 
		}
	}
}
</script>

<style scoped>
#nav {
	background:whitesmoke;	
}
#brand {
	color: darkgray;
}
</style>