import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	Strict: true,
	state: {
        metas: {
            project: "My example project",
            descrip: "Example data for testing VUEX. Using Cytoscape graph elements structure",
            creator: "Ceri Binding, University of South Wales",
            contact: "ceri.binding@southwales.ac.uk",
            created: "2021-01-07",
            updated: "2021-01-07", 
            version: "1.0",           
        },        
        graph: {
            nodes: [],
            edges: []
        }	
    },	
    
	getters: {
        nodeById: state => id => state.graph.nodes.find(n => n.id === id),
        edgeById: state => id => state.graph.edges.find(e => e.id === id),

		phases: state => state.graph.nodes.filter(n => n.class === "phase"),
		groups: state => state.graph.nodes.filter(n => n.class === "group"),
        subgroups: state => state.graph.nodes.filter(n => n.class === "subgroup"),
        contexts: state => state.graph.nodes.filter(n => n.class === "context"), 

        cyNodes: state => {
            return state.graph.nodes.map(n => {
                return { data: n }
            })
        },

        cyEdges: state => {
            return state.graph.edges.map(e => {
                //if(!e.id) e.id = `${e.class || 'node'}-${e.source || 'source'}-${e.target || 'target'}`
                return { data: e }
            })
        },

        cyElements: (state, getters) => {
            return []
                .concat(getters.cyNodes)
                .concat(getters.cyEdges)            
        }
        
        //nextPhaseID: state => { return 0 },
        //nextGroupID: state => { return 0 },
        //nextContextID: state => { return 0 },
        //newPhase: state => { return { id: "pXX", type: "phase", phaseID: "XX", label: "New phase" }},
        //newGroup: state => { return {}},
        //newSubGroup: state => { return {}},
        //newContext: state => { return {}},
	},
    //code modified from https://codeburst.io/build-a-simple-todo-app-with-vue-js-1778ae175514
    // mutations are synchronous, keep them as simple as possible
	mutations: {	
        ADD_NODE(state, node) {
            const nodes = state.graph.nodes            
            const index = nodes.findIndex(n => n.id === node.id) 
            if(index === -1)  
                nodes.push(node)                // doesn't exist - add 
            else 
                nodes.splice(index, 1, node)    // exists - replace 
        },        
        EDIT_NODE(state, node) {
            const nodes = state.graph.nodes
            const index = nodes.findIndex(n => n.id === node.id)  //nodes.indexOf(node)     
            if(index !== -1)                    // exists 
                nodes.splice(index, 1, node)    // replace  
        },
        REMOVE_NODE(state, node) {
            const nodes = state.graph.nodes
            const index = nodes.findIndex(n => n.id === node.id)
            if(index !== -1)                    // exists
                nodes.splice(index, 1)          // remove 
        },
        REMOVE_NODES(state) {
            state.graph.nodes = []
        },

        ADD_EDGE(state, edge) {
            const edges = state.graph.edges   
            if(!edge.id)
                edge.id = `${edge.class || 'node'}-${edge.source || 'source'}-${edge.target || 'target'}`         
            const index = edges.findIndex(e => e.id === edge.id)
            if(index === -1) 
                edges.push(edge)                // doesn't exist - add         
            else 
               edges.splice(index, 1, edge)     // exists - replace            
        },        
        EDIT_EDGE(state, edge) {
            const edges = state.graph.edges
            const index = edges.findIndex(e => e.id === edge.id)
            if(index !== -1)                    // exists 
                edges.splice(index, 1, edge)    // replace 
        },
        REMOVE_EDGE(state, edge) {
            const edges = state.graph.edges
            const index = edges.findIndex(e => e.id === edge.id)
            if(index !== -1)            // exists
                edges.splice(index, 1)  // remove 
        },
        REMOVE_EDGES(state) {
            state.graph.edges = []
        }		
	},
	actions: {	
        async loadPhaserData({commit, dispatch}, data){
            // actions are asynchronous so ensure order else chaos..
            await dispatch('clearAll', commit)
            await dispatch('addNodes', data.nodes || [], commit)
            await dispatch('addEdges', data.edges || [], commit)
        },
        saveMatrixData(){},

        clearAll({commit}){
            // mutations are synchronous so this is OK
            commit('REMOVE_NODES')
            commit('REMOVE_EDGES')
        },	
        addNode({commit}, node){ 
            commit('ADD_NODE', node)            
        },
        addNodes({commit, dispatch}, nodes){
            nodes.forEach(node => dispatch('addNode', node, commit))			
        },
		editNode({commit}, node){
			commit('EDIT_NODE', node)
		},
		removeNode({commit}, node){
			commit('REMOVE_NODE', node)
        },
        
        addEdge({commit}, edge){
            commit('ADD_EDGE', edge)            
        },
        addEdges({commit, dispatch}, edges){
			edges.forEach(edge => dispatch('addEdge', edge, commit))	
        },
		editEdge({commit}, edge){
			commit('EDIT_EDGE', edge)
		},
		removeEdge({commit}, edge){
			commit('REMOVE_EDGE', edge)
        },    
        
		/*addContext({commit}, context){
			commit('ADD_NODE', context)
        },
        addContexts({commit}, contexts){
			commit('ADD_NODES', contexts)
        },
		editContext({commit}, context){
			commit('EDIT_NODE', context)
		},
		removeContext({commit}, context){
			commit('REMOVE_NODE', context)
        } */       
	}	
})