<template>	
	<!--cytoscape graph-->
    <div id="holder">
    <cytoscape id="diagram" ref="cy" :config="config" :preConfig="preConfig" class="bg-white p-3">
        <!--<cy-element v-for="def in elements.nodes"
            :key="`${def.data.id}`"
            :definition="def"/>
        <cy-element v-for="def in elements.edges.filter(e => e.data.type == 'above')"
            :key="`${def.data.id}`"
            :definition="def"/>-->
        <cy-element v-for="def in elements"
            :key="`${def.data.id}`"
            :definition="def"/>
    </cytoscape>
    </div>
	
</template>

<script>
//import MyComponent from '@/components/MyComponent.vue'
import dagre from 'cytoscape-dagre'

export default {
	name: 'PhaserDiagramCytoscape',
	components: {
		//MyComponent 
	},
	mixins: [ ],
	props: { },
	data() {
		return {
            selectedElementID: "",           
            config: {
                layout: {
                    name: 'dagre', // todo, snap to grid during or after layout?
                    // dagre options, uses default value if undefined
                    nodeSep: 20, // the separation between adjacent nodes in the same rank
                    //edgeSep: 20, // the separation between adjacent edges in the same rank                    
                    rankSep: 50, // the separation between each rank in the layout
                    marginx: 20,
                    marginy: 20,                    
                    rankDir: 'TB', // 'TB' for top to bottom flow, 'LR' for left to right
                    ranker: undefined, // Type of algorithm to assign a rank to each node in the input graph. Possible values: 'network-simplex', 'tight-tree' or 'longest-path'
                    //minLen: function( edge ){ return 1; }, // number of ranks to keep between the source and target of the edge
                    //edgeWeight: function( edge ){ return 1; }, // higher weight edges are generally made shorter and straighter than lower weight edges

                    // general layout options
                    fit: true, // whether to fit to viewport
                    padding: 50, // fit padding
                    spacingFactor: undefined, // Applies a multiplicative factor (>0) to expand or compress the overall area that the nodes take up
                    nodeDimensionsIncludeLabels: false, // whether labels should be included in determining the space used by a node
                    animate: false, // whether to transition the node positions
                    boundingBox: undefined, // constrain layout bounds; { x1, y1, x2, y2 } or { x1, y1, w, h }
                    transform: function( node, pos ){ return pos; }, // a function that applies a transform to the final node position
                    ready: function(){}, // on layoutready
                    stop: function(){
                        //generate PNG image to display from "id = downloadPNG"
                        //var png64 = cy.png();
                        //$('#downloadPNG').attr('href', png64);
                    } // on layoutstop              
                },
                style: [
                    {	
                        // see https://github.com/cytoscape/cytoscape.js/blob/master/documentation/md/style.md
                        // for style settings. Note can layer and position SVG background images - to maybe use
                        // images for finds and samples indicators
                        selector: 'node[class="context"]',
                        style: {
                            'font-family': 'sans-serif',
                            'font-size': '1em',
                            'shape': 'rectangle',
                            'width': '200',
                            'height': '50',
                            'label':  el => `${el.data('identifier')}: ${el.data('type')}`, 
                            'padding': 0,
                            'color': 'black',
                            'text-opacity': 1.0,
                            'text-valign': 'center',
                            'text-halign': 'center',
                            'background-color': 'white',
                            'background-opacity': 1.0,
                            'border-color': 'black',
                            'border-opacity': 1.0,
                            'border-width': '1px',
                            'border-style': 'solid',                            
                        }
                    },
                    {
                        selector: 'node[class="phase"]',
                        style: {
                            'color': 'black',
                            'shape': 'rectangle',
                            //'content': 'data(label)',
                            'label':  el => `Phase ${el.data('identifier')}`, 
                            'text-opacity': 0.5,
                            'text-valign': 'bottom',
                            'text-halign': 'center',
                            'background-color': 'ivory',
                            'border-width': '1px'
                        }
                    },
                    {
                        selector: 'node[class="group"]',
                        style: {
                            'color': 'green',
                            'shape': 'round-rectangle',
                            //'content': 'data(label)',
                            'label':  el => `Group ${el.data('identifier')}`, 
                            'text-opacity': 0.5,
                            'text-valign': 'bottom',
                            'text-halign': 'center',
                            'background-color': 'honeydew',
                            'border-color': 'green',
                            'border-width': '1px'
                        }
                    },
                    {
                        selector: 'node[class="subgroup"]',
                        style: {
                            'color': 'blue',
                            'shape': 'round-rectangle',
                            //'content': 'data(label)',
                            'label':  el => `Sub-group ${el.data('identifier')}`, 
                            'text-opacity': 0.5,
                            'text-valign': 'bottom',
                            'text-halign': 'center',
                            'background-color': 'aliceblue',
                            'border-color': 'blue',
                            'border-width': '1px'
                        }
                    },			
                    {
                        selector: 'edge',
                        style: {
                            'curve-style': 'taxi',
                            'taxi-direction': 'downward',
                            'taxi-turn': 50,
                            'edge-distances': 'node-position',
                            'taxi-turn-min-distance': 40,
                            'source-endpoint': 'outside-to-node',
                            'target-endpoint': 'outside-to-node',
                            'width': 2,
                            'target-arrow-shape': 'none',
                            'line-color': 'gray',
                            'line-style': 'solid',
                            'target-arrow-color': '#9dbaea'
                        }
                    }
                ]                
            }
		}
	},
	computed: {
        elements() {
            return this.$store.getters.cyElements
        }             
     },
	methods: {
        preConfig (cytoscape) {
            cytoscape.use(dagre)          
        },
        //afterCreated(cytoscape)  {
           // cytoscape.layout(this.config.layout).run() //temp 
        //},
        runLayout() {
            let cyi = this.$refs.cy.instance
            cyi.layout(this.config.layout).run()
        },
        zoomToFit(){
            let cyi = this.$refs.cy.instance
            cyi.fit()
        },
        zoomIn(){
            let cyi = this.$refs.cy.instance
            cyi.zoom(1) // TODO
        },
        zoomOut(){
            let cyi = this.$refs.cy.instance
            cyi.zoom(1) // TODO
        }
    },

	// lifecycle hooks
	beforeCreate() {},
    created() {},    
	beforeMount() {},
	mounted() {
        let self = this
        
        let cyi = self.$refs.cy.instance
        cyi.on('click tap', 'node', function(evt) {
            self.selectedElementID = evt.target.id()                
        });
        // override height setting on cytoscape div, else always 600px!
        // see https://github.com/rcarcasses/vue-cytoscape/issues/47
        document.getElementById("cytoscape-div").style.minHeight="900px";
                
        // actions in response to event bus messages (from menu bar)
        self.$root.$on("redoLayout", () => self.runLayout())
        self.$root.$on("zoomToFit", () => self.zoomToFit())
        self.$root.$on("zoomIn", () => self.zoomIn())
        self.$root.$on("zoomOut", () => self.zoomOut())
    },
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

<style scoped>
    
</style>